body {
  margin: 0;
  padding: 0;
  padding-bottom: 50px;
}

button:focus {
  outline: none;
}

textarea {
  border-bottom: 2px solid rgba(0, 129, 141, 0.074) !important;
}

textarea:focus {
  outline: 0;
  border-bottom: 2px solid #00828D !important;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.container-main {
  display: flex;
  flex-direction: row;;
  padding-top: 15px;
}

#question {
  display: flex;
  flex: 6 1;
  flex-direction: column;
  width: 100%;
  align-items: center;
}

#question-columns {
  display: flex;
  flex-direction: row;
  margin-left: 170px;
  margin-bottom: -15px;
}

#summary{
  flex:2 1;
  text-align: center;
  border-left: 2px solid black;
  padding: 10px;
  align-items: center;
}

#summary > h3 {
  margin-bottom: 35px;
  text-align: left;
  margin-left: 30px;
}

#summary p {
  font-size: 13px;
  text-align: left;
  margin-left: 30px;
}

div.row {
  display: flex;
  flex-direction: row;
  margin-bottom: -20px;
}

.save-btn {
  width: 160px;
  padding: 10px 15px;
  margin-top: 50px;
  margin-left: 100px;
  align-self: center;
}

div div.label-container:last-of-type {
  -webkit-animation: slide-up 1.4s ease;
          animation: slide-up 1.4s ease;
}

div#question-rows div.label-container:last-of-type {
  -webkit-animation: slide-up 1.4s ease;
          animation: slide-up 1.4s ease;
}

@-webkit-keyframes slide-up {
  0% {
      opacity: 0.3;
  }
  100% {
      opacity: 1;
  }
}

@keyframes slide-up {
  0% {
      opacity: 0.3;
  }
  100% {
      opacity: 1;
  }
}
.container-dashboard {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 20px 30px;
}

.question-tab {
  width: 80vw;
  padding: 15px 30px;
  margin-bottom: 20px;
  border: 1px solid black;
  position: relative;
}

.question-title-link {
  color: black;
}

.buttons-wrapper {
  position: absolute;
  right: 10px;
  top: 10px;
}

button.add-question-btn {
  align-self: start;
  margin-left: 40px;
  margin-bottom: 20px;
}

span.add-question-btn-sm {
  margin: 15px;
}


.dashboard-tab-btn {
  margin-left: 10px;
}

.dashboard-tab-btn img {
  padding: 10px;
  width: 20px;
  height: 20px;
}
